___CSS_LOADER_EXPORT___.push([module.id, `*{
    margin:0;
    padding:0;
}
html, body{
    width: 100vw;
    height: 100vh;
    min-width: 100vw;
    min-height: 100vh;
}
body{
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr 5fr; 
}
.header{
    background-color: #5367E2;
    grid-column: 1/3;
    grid-row: 1/2;
    text-align: center;
    padding-top: 7%;
    font-size: 3rem;
}
.sidebar{
    background-color: #8e9df8;
    grid-column: 1/2;
    grid-row: 2/3;
    padding: 20px;
}
.content{
    background-color: #e7eaff;
    grid-column: 2/3;
    grid-row: 2/2;
}
.sort{
    display: flex;
    flex-direction: column;
    gap: 30px;
    text-decoration: none;
    font-size: 20px;
    color: #ffffff;
}
.sort>div{
    display: flex;
    gap: 10px;
}
.project{
    background-color: black;
    width: 30%;
    height: 30%;
}
